.game-item{background-size:cover;width:65px;height:65px;position:absolute}.game-item-banana{background-image:url(../assets/banana.png)}.game-item-flower{background-image:url(../assets/flower.png)}.game-item-line-1{left:15px}.game-item-line-2{left:110px}.game-item-line-3{left:205px}.game-item-transition{bottom:1000px;-webkit-transition:opacity 0.3s linear,bottom 0.35s ease-out;transition:opacity 0.3s linear,bottom 0.35s ease-out}.game-item-enter{opacity:0}.game-item-leave{opacity:0;bottom:-65px;-webkit-transition:opacity 0.1s ease-out,bottom 0.1s ease-out;transition:opacity 0.1s ease-out,bottom 0.1s ease-out}



